@mixin list-zeroing {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}

@mixin margin-zeroing {
  margin-top: 0;
  margin-bottom: 0;
}

@mixin block-centering-mobile {
  width: 260px;
  margin-right: auto;
  margin-left: auto;
}

@mixin block-centering-tablet {
  width: 668px;
  margin-right: auto;
  margin-left: auto;
}

@mixin block-centering-desktop {
  width: 1048px;
  margin-right: auto;
  margin-left: auto;
}
