.zigzag-white {
  position: relative;
}

@media (min-width: 768px) {
  .zigzag-white::after {
    position: absolute;
    content: "";
    bottom: -1px;
    left: 0;
    z-index: 4;
    width: 100%;
    height: 24px;
    background-image: url("../img/icons/zigzag/white/icon-zigzag-fill-tablet.png");
    background-repeat: repeat-x;
  }
}
@media (min-width: 1150px) {
  .zigzag-white::after {
    bottom: -5px;
    height: 34px;
    background-image: url("../img/icons/zigzag/white/icon-zigzag-fill-desktop.png");
  }
}

.zigzag-grey {
  position: relative;
}

.zigzag-grey::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  z-index: 4;
  width: 100%;
  height: 6px;
  background-image: url("../img/icons/zigzag/grey/icon-zigzag-mobile.png");
  background-repeat: repeat-x;
}
@media (min-width: 768px) {
  .zigzag-grey::after {
    bottom: 2px;
    height: 17px;
    background-image: url("../img/icons/zigzag/grey/icon-zigzag-tablet.png");
  }
}
@media (min-width: 1150px) {
  .zigzag-grey::after {
    left: calc((100% - 1050px) / 2);
    width: 1050px;
    height: 23px;
    background-image: url("../img/icons/zigzag/grey/icon-zigzag-desktop.png");
  }
}

/* stylelint-disable selector-list-comma-newline-after */
*, *::before, *::after {
  box-sizing: inherit;
}

/* stylelint-enable selector-list-comma-newline-after */
img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

