  position: relative;
  width: 39px;
  height: 39px;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .control__mark {
    width: 50px;
    height: 50px;
  }
}

.control__input:hover + .control__mark {
  border-color: #62d1ba;
}

.control__input:focus ~ .control__mark {
  border-color: rgba(0, 0, 0, 0.3);
  outline: none;
}

.control__input:active + .control__mark {
  border-color: rgba(0, 0, 0, 0.3);
}

.control__input:disabled + .control__mark,
.control__input:disabled:hover + .control__mark {
  border-color: #444444;
  opacity: 0.3;
}

.control__label {
  grid-column: 2/-1;
}

.control__input:disabled ~ .control__label,
.control__input:disabled:hover ~ .control__label {
  color: rgba(0, 0, 0, 0.3);
}

/* Checkbox */
.control__input[type=checkbox]:checked + .control__mark {
  position: absolute;
  content: "";
